<template>
  <el-dialog @open="openDialog" @close="closeDialog" width="500px" :title="title" :visible="value">
    <div class="img-warper" v-if="!loading">
      <img :src="src" :alt="title" @error="nofind" v-if="!imgError">
      <div class="img-error" v-else>
        
      </div>
    </div>
  </el-dialog>
</template>
<script>
export default {
  name: 'pic-view',
  data () {
    return {
      imgError: false,
      loading: true,
    }
  },
  props: {
    /**
     * 
     */
    title: {
      type: String,
      default: ''
    },
    /**
     * 
     */
    src: {
      type: String,
      default: ''
    },
    /**
     * 
     */
    value: {
      type: Boolean
    },
  },
  methods: {
    /**
     * 
     */
    openDialog () {
      this.imgError = false
      this.loading = true
      this.$nextTick(() => {
        this.loading = false
      })
    },
    /**
     * 
     */
    closeDialog () {
      this.$emit('input', false)
    },
    /**
     * 
     */
    nofind () {
      this.imgError = true
    }
  },
}
</script>
<style scoped>
.img-warper{
  margin: 0 auto;
  padding:20px;
}
.img-error{
  line-height: 50px;
  text-align: center;
}
.img-warper img{
  width:100%;
}
</style>
